.home {
}

.home__header {
    display: flex;
    justify-content: center;
    color: #6C757D;
}

.submit_btn {
    background-color: #343A40;
    border: none;
    color: #6C757D;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    transition-duration: 0.4s;
}

.submit_btn:hover {
    background-color: #495057; /* Green */
    color: white;
}

hr {
    margin-top: 10px;
    margin-bottom: 10px;
    border-color: #6C757D;
}

.link_header {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left: 15%;
    color: #6C757D;
}

.link_input {
    width: 80%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    color: #ADB5BD;
}

.borderBottom {
    margin-top: 10px;
    margin-bottom: 10px;
    border-bottom: 1px #ADB5BD solid;
    width: 80%;
}

.error_msg {
    color: #b32a2a;
}

.home__my_intro {
    padding: 0 10%;
    display: flex;
    justify-content: flex-start;
    color: #6C757D;
}
