li, a, button {
    font-weight: 500;
    font-size: 16px;
    text-decoration: none;
}

nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 30px 90px;
}

.logo {
    cursor: pointer;
    margin-right: auto;
    color: #6c757d;
    font-weight: bold;
}

.logo:hover {
    color: #adb5bd;
}

.nav__links {
    list-style: none;
}

/* Displays li as inline in big screen and list in small screens !! */
.nav__links li {
    display: inline-block;
    padding: 0 20px;
}

.nav__links li a {
    transition: all 0.3s ease 0s;
    color: #6c757d;
    font-weight: bold;
}

.nav__links li a:hover {
    color: #adb5bd;
}

.nav__inner_links li {
    display: none;
    align-items: flex-end;
    flex-direction: column;
}
